import React, {Component} from 'react';
import Todos from './components/Todos';
import Header from "./components/layout/Header";
import AddTodos from "./components/AddTodos";
import './App.css';
var lastID = 3;
class App extends Component {
	state = {
		todos: [
			{
				id: 1,
				title: 'Task1 7AM',
				completed: true
			},
			{
				id: 2,
				title: 'Task2 7AM',
				completed: false
			},
			{
				id: 3,
				title: 'Task3 7AM',
				completed: false
			},
		]
	};

	// Toggle complete
	markComplete = (id) => {
		this.setState({
			todos: this.state.todos.map(todo => {
				if (todo.id === id) todo.completed = !todo.completed
				return todo;
			})
		})
	};

	// Delete item
	markDelete = (id_clicked) => {
		this.state.todos.splice( //cool way to delete item using index
			this.state.todos.findIndex(({id}) => { //find item by id, return index
				return (id === id_clicked)
			}),
			1);
		console.log(this.state.todos);
		//Propagate changes
		this.setState({
			todos: this.state.todos
		})
	};

	// Add Todo
	addTodo = (title) => {
		console.log(title)
		this.setState({
			todos:
				this.state.todos.concat({
					id:++lastID,
					title:title,
					completed:false
				})
		})
	};
	render() {
		return (
			<div className="App">
				<Header></Header>
				<AddTodos addTodo={this.addTodo}></AddTodos>
				<Todos todos={this.state.todos}
							 markComplete={this.markComplete}
							 markDelete={this.markDelete}
				/>
			</div>
		);
	}
}

export default App;
