import React, {Component} from 'react';

class TodoItem extends Component {
	getClass = () =>{
		var ret = 'todoItem';
		ret = this.props.todo.completed ? ret + ' completedItem': ret;
		return ret;
	};
	render() {
		const {id, title, completed} = this.props.todo;
		return (
			<div className={this.getClass()//function with () return values} //style={this.getStyle()
			}
			>
				<p onClick={this.props.markComplete.bind(this, id)}	>
					<input type="checkbox"
								 checked={completed}
								 onChange={() => {}}
					/>
					{id} {title}
					<button className={'delBtn'} onClick={this.props.markDelete.bind(this, id)}>
						X
					</button>
				</p>
			</div>
		)
	}
}


class Todos extends Component {
	render() {
		return this.props.todos.map((todo) =>
			<TodoItem key={todo.id}
								todo={todo}
								markComplete={this.props.markComplete}
								markDelete={this.props.markDelete}

			/>
		);
	}
}

export default Todos;
